import { createStore, combineReducers, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';
// import { logger } from 'redux-logger';
import reducer from './Reducer/reducer';
import personReducer from './Person/reducer';
import walletReducer from './Wallet/reducer';
import settingReducer from './Setting/reducer';
import createWalletReducer from './CreateWallet/reducer';
import sendingCoinReducer from './SendingCoin/reducer';
import nodeManageReducer from './NodeManage/reducer';
import NotificationControllerReducer from './NotificationController/reducer';

export const rootReducer = combineReducers({
  reducer,
  personReducer,
  walletReducer,
  settingReducer,
  createWalletReducer,
  sendingCoinReducer,
  nodeManageReducer,
  NotificationControllerReducer,
});

const middleware = [thunk];
// if (process.env.NODE_ENV === 'development') {
//   middleware.push(logger);
// }

const store = createStore(rootReducer, applyMiddleware(...middleware));
export default store;
