import React, { useEffect, useRef, useState } from 'react';
import styled from 'styled-components';
import World from '@assets/icons/world';
import LoadPage from '@assets/icons/loadPage';
import Download from '@assets/icons/download';
import Cate from '@assets/icons/cate';
import Open from '@assets/icons/open';
import Setting from '@assets/icons/setting';
import Page from '@assets/icons/page';
import HiddenCate from '@assets/icons/hiddenCate';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faChevronLeft,
  faChevronRight,
  faPlus,
} from '@fortawesome/free-solid-svg-icons';
import { Animated } from 'react-animated-css';
import { Tooltip } from '@@packages/components';
import { SystemCore } from '@@packages/core/src';

function BlockSearch({ type = 'download', isBack = false, search = '', isNext = false }) {
  const [value, setValue] = useState('');
  const inputRef = useRef();
  const [showMenu, setShowMenu] = useState(false);
  const [isFocused, setIsFocused] = useState(false);
  const [animationOut, setAnimationOut] = useState(false);
  const [back, setBack] = useState(false)
  const [next, setNext] = useState(false)
  const [pages, setPages] = useState(1)

  useEffect(() => {
    setValue(search);
  }, [search])

  useEffect(() => {
    SystemCore.on("on-keyboard-show-hide", (res) => {
      if (res.success) {
        const isShow = res.data.isShow;
        if (isShow) {
          e.target.select();
          setIsFocused(true)
        } else {
          setIsFocused(false);
          setShowMenu(false);
          setAnimationOut(false);
          inputRef.current.blur();
        }
      }
    });
  }, [])

  const viewTab = () => {
    setShowMenu(false);
    SystemCore.send({
      "command": "view-all-tab",
    })
  }
  const onNext = () => {
    setShowMenu(false);
    if (!isNext) return;
    SystemCore.send({
      command: "next",
    })
  }
  const onBack = () => {
    setShowMenu(false);
    SystemCore.send({
      command: "back",
    })
  }
  const reloadPage = () => {
    setShowMenu(false);
    SystemCore.send({
      command: "reload",
    })
  }
  const handleKeyDown = (e) => {
    if (e.keyCode === 13) {
      e.preventDefault();
      e.target.blur();
      handleSearch();
    }
  }
  const handleSearch = () => {
    setShowMenu(false);
    SystemCore.send({
      command: "search",
      value: value,
    })
    inputRef.current.blur();
  }

  const newTab = () => {
    
    setShowMenu(false);
    SystemCore.send({
      command: "new-tab",
    })
  }
  const showCategory = () => {
    setShowMenu(false);
    SystemCore.send({
      command: "show-modal-categories"
    })
  }

  const renderIcon = () => {
    switch (type) {
      case 'download':
        return (
          <Tooltip content="Install D-App" direction="top">
            <div className="cicle type1">
              <Download />
            </div>
          </Tooltip>
        );
      case 'add':
        return (
          <div className="cicle type1">
            <FontAwesomeIcon icon={faPlus} color="white" fontSize={14} />
          </div>
        );
      case 'open':
        return (
          <Tooltip content="Open D-App" direction="top">
            <div className="cicle type2">
              <Open />
            </div>
          </Tooltip>
        );
      default:
        return <></>;
    }
  };

  // const handleSearch = () => {
  //   SystemCore.send({
  //     command: 'search',
  //     value,
  //   });
  //   SystemCore.on('update-navigate', (res) => {
  //     if (res.success) {
  //       const { canGoBack, canGoForward, url } = res.data;
  //       setBack(canGoBack);
  //       setNext(canGoForward);
  //       setValue(url);
  //     }
  //   });
  // };

  const handleKeyEnter = (e) => {
    if (e.keyCode === 13) {
      handleSearch();
    }
  };

  const HandleNext = () => {
    SystemCore.send({
      command: 'next',
    });
    SystemCore.on('update-navigate', (res) => {
      if (res.success) {
        const { canGoBack, canGoForward, url } = res.data;
        setBack(canGoBack);
        setNext(canGoForward);
        setValue(url);
      }
    });
  };
  const HandleBack = () => {
    SystemCore.send({
      command: 'back',
    });
    SystemCore.on('update-navigate', (res) => {
      if (res.success) {
        const { canGoBack, canGoForward, url } = res.data;
        setBack(canGoBack);
        setNext(canGoForward);
        setValue(url);
      }
    });
  };

  const navigateBrowserTabs = () => {
    SystemCore.send({
      command: 'navigate-browser-tabs',
    }).then((res) => {
      if (res.success) {
        //
      }
    });
  };

  useEffect(() => {
    // SystemCore.send({
    //   command: 'amount-tab',
    // });
    // SystemCore.on('focus-search', (res) => {
    //   if (res.success) {
    //     setPages(res.amount);
    //   }
    // });
    SystemCore.on('update-navigate', (res) => {
      if (res.success) {
        const { canGoBack, canGoForward, url } = res.data;
        setBack(canGoBack);
        setNext(canGoForward);
        setValue(url);
      }
    });
  }, []);


  const handleAddTab = () => {
    SystemCore.send({
      command: 'new-tab',
    }).then((res) => {
      if (res.success) {
        setValue('');
        setPages(res.amount);
      }
    });
    SystemCore.on('update-navigate', (res) => {
      if (res.success) {
        const { canGoBack, canGoForward, url } = res.data;
        setBack(canGoBack);
        setNext(canGoForward);
        setValue(url);
      }
    });
  };

  return (
    <Wrapper>
      <WrapperInput>
        {isBack ? 
          <ContainBack onClick={onBack}>
            <FontAwesomeIcon icon={faChevronLeft} color="#D3D7DB" fontSize={22} />
          </ContainBack>
        : (
          <ContainBack>
            <World/>
          </ContainBack>
        )}
        <div className='splash'></div>
        <Input
          ref={inputRef}
          type={"url"}
          value={value}
          placeholder="Search or enter website address"
          onChange={(e) => setValue(e.target.value)}
          onFocus={(e) => {
            e.target.select();
            setIsFocused(true)
          }}
          onBlur={() => {
            setIsFocused(false);
            setShowMenu(false);
            setAnimationOut(false);
          }}
          onKeyDown={handleKeyDown}
        />
        {isFocused ? (
          <div onClick={handleSearch}>
            <img
              src={btn}
              alt=""
              style={{ width: 28, height: 28 }}
            />
          </div>
        ) : (
          <>
            {showMenu &&
              <Animated
                isVisible={showMenu}
                animationIn="fadeInRight"
                animationOut={!animationOut ? '' : 'fadeOutRight'}
                animationInDuration={800}
                animationOutDuration={800}
                className="wrap-menu">
                <div className="icon" onClick={showCategory}>
                  <Setting />
                </div>
                {/* <div className="icon" onClick={viewTab}>
                  <Page />
                </div> */}
                {isNext &&
                  <div className="icon"  onClick={onNext}>
                    <FontAwesomeIcon
                      icon={faChevronRight}
                      color="white"
                      fontSize={22}
                    />
                  </div>
                }
                <div className="icon">
                  <div className="cicle" onClick={newTab}>
                    <FontAwesomeIcon
                      icon={faPlus}
                      color="#554CFF"
                      fontSize={14}
                    />
                  </div>
                </div>
                <div className="icon" onClick={() => setShowMenu(false)}>
                  <HiddenCate />
                </div>
              </Animated>
            }
            {!value ? (
              // <div className="icon color-1" onClick={viewTab}>
              //   <Page />
              // </div>
              <></>
            ) : (
              <>
                <div className="icon" onClick={reloadPage}>
                  <LoadPage />
                </div>
                {/* <div className="icon">{renderIcon()}</div> */}
              </>
            )}
            <div
              className="icon"
              onClick={() => {
                setShowMenu(true);
                setAnimationOut(true);
              }}>
              <Cate />
            </div>
          </>
        )}
      </WrapperInput>
    </Wrapper>
  );
}

export default React.memo(BlockSearch);

