const Input = styled.input`
  width: 100%;
  height: 100%;
  border: none;
  padding: 0px 16px;
  ::placeholder {
    color: #d3d7db;
  }
`;

const ContainBack = styled.div`
  min-width: 44px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 6px;
`

