import { PersonActionTypes } from './action';

const initState = {
  personInfo: {},
  status: 0,
  transaction_list: [],
  errorSetPersonInfo: '',
  statusSetPersonInfo: -1,
};

export default function personReducer(state = initState, action) {
  switch (action.type) {
    case PersonActionTypes.GET_PERSON_INFO:
      return {
        ...state,
        status: 0,
      };
    case PersonActionTypes.GET_PERSON_INFO_SUCCESS:
      return {
        ...state,
        personInfo: action.payload.data,
        status: 1,
      };
    case PersonActionTypes.SET_PERSON_INFO:
      return {
        ...state,
        statusSetPersonInfo: 0,
      };
    case PersonActionTypes.SET_PERSON_INFO_SUCCESS:
      return {
        ...state,
        personInfo: action.payload,
        statusSetPersonInfo: 1,
      };
    case PersonActionTypes.SET_PERSON_INFO_FAIL:
      return {
        ...state,
        errorSetPersonInfo: action.payload,
        statusSetPersonInfo: 2,
      };
    case PersonActionTypes.SET_TRANSACTION_LIST:
      return {
        ...state,
        transaction_list: [...action.payload],
      };

    case PersonActionTypes.SET_ITEM_TRANSACTION:
      return {
        ...state,
        transaction_list: [action.payload, ...state.transaction_list],
      };

    case PersonActionTypes.SET_SHOW_AVATAR:
      return {
        ...state,
        personInfo: { ...state.personInfo, show_avatar: action.payload },
      };

    default:
      return { ...state };
  }
}
