import { NotificationControllerActionTypes } from './action';

const initState = {
  isVisible: false,
  contentNoti: {},
};

export default function NotificationControllerReducer(
  state = initState,
  action,
) {
  switch (action.type) {
    case NotificationControllerActionTypes.SET_MODAL_NOTI_VISIBLE: {
      const payload = action.payload;
      return {
        ...state,
        isVisible: payload.isVisible,
        contentNoti: payload.contentNoti || {},
      };
    }
    default:
      return { ...state };
  }
}
