import React from 'react';
import { Button, ModalBase, Thumb } from '@@packages/components';
import styled from 'styled-components';

function ModalRenew({item, onClose, onRenew}) {
  return (
    <ModalBase visible={item} onClose={onClose}>
        <Thumb />
        <Item>
          <div className="icon">
            {item?.Icon}
          </div>
          <span>{item?.value}</span>
        </Item>
        <p>Bạn đang đăng ký gói ... . Nếu bạn chọn đăng ký gói ... thì gói đăng ký trước sẽ bị hủy. Bạn chấp nhận đăng ký?</p>
        <WrapperButton>
          <Button title="Cancel" variant="outlined"  onClick={() => onClose?.()}/>
          <Button title="ReNew" onClick={() => {onRenew?.(item?.id - 1);onClose?.();}}/>
        </WrapperButton>
    </ModalBase>
  );
}

export default ModalRenew;

const WrapperButton= styled.div`
  display: flex;
  gap: 10px;
  margin-top: 10px;
`;
const Item= styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
  margin: 8px 0;
    .icon {
      width: 32px;
      height: 32px;
      background: #004DFF;
      border-radius: 8px;
      color: white;
      display: flex;
      align-items: center;
      justify-content: center;
    }
`;