import { SystemCore } from '@@packages/core';

export const NodeManageActionTypes = Object.freeze({
  GET_NODE_LIST: 'GET_NODE_LIST',
  GET_NODE_LIST_SUCCESS: 'GET_NODE_LIST_SUCCESS',
  GET_NODE_LIST_FAIL: 'GET_NODE_LIST_FAIL',
  CONNECT_NODE: 'CONNECT_NODE',
  CONNECT_NODE_SUCCESS: 'CONNECT_NODE_SUCCESS',
  CONNECT_NODE_FAIL: 'CONNECT_NODE_FAIL',
});

export function getNodes(dispatch) {
  dispatch({ type: NodeManageActionTypes.GET_NODE_LIST });
  SystemCore.send({
    command: 'get-node',
  })
    .then((res) => {
      dispatch({
        type: NodeManageActionTypes.GET_NODE_LIST_SUCCESS,
        payload: res,
      });
    })
    .catch((err) => {
      dispatch({
        type: NodeManageActionTypes.GET_NODE_LIST_FAIL,
        payload: err,
      });
    });
}
export const connectNode = (listWallet, node, callback) => async (dispatch) => {
  //   dispatch({ type: NodeManageActionTypes.CONNECT_NODE });

  SystemCore.send({
    command: 'connect-node',
    value: {
      wallets: listWallet,
      node,
    },
  })
    .then((res) => {
      callback({ status: 0 });
    })
    .catch((err) => {
      callback({ status: 1 });
    });
};
