import React, { CSSProperties } from "react";
import classNames from "classnames";
import { mapModifiers } from "@@packages/utils";

export interface TypographyProps {
  variant?: "h1" | "h2" | "h3" | "h4" | "h5" | "h6" | "body1" | "body2" | "body3" | "body4" | "9x15" | "80x100";
  label: React.ReactNode;
  textColorClass?: TTextColor;
  opacity?: number;
  onClick?: () => void;
  className?: string;
  style?: CSSProperties;
}

const mapType = {
  h1: "h1",
  h2: "h2",
  h3: "h3",
  h4: "h4",
  h5: "h5",
  h6: "h6",
  body1: "p",
  body2: "p",
  body3: "p",
  body4: "p",
};

const Typography: React.FC<TypographyProps> = ({
  variant = "body1",
  label,
  textColorClass,
  opacity,
  style,
  onClick,
  className,
}) => {
  return React.createElement(
    typeof label !== "string" && typeof label !== "number" ? "div" : mapType[variant] || "p",
    {
      className: classNames(
        className,
        { "text-white": textColorClass ? false : true },
        mapModifiers("a-typography", variant),
        textColorClass as any
      ),
      onClick: onClick,
      style: {
        opacity: opacity,
        ...style,
      },
    },
    label
  );
};

export default React.memo(Typography);
