import { SystemCore } from '@@packages/core';
import createAction from '../action';

export const PersonActionTypes = Object.freeze({
  GET_PERSON_INFO: 'GET_PERSON_INFO',
  GET_PERSON_INFO_SUCCESS: 'GET_PERSON_INFO_SUCCESS',
  GET_PERSON_INFO_FAIL: 'GET_PERSON_INFO_FAIL',
  SET_PERSON_INFO: 'SET_PERSON_INFO',
  SET_PERSON_INFO_SUCCESS: 'SET_PERSON_INFO_SUCCESS',
  SET_PERSON_INFO_FAIL: 'SET_PERSON_INFO_FAIL',
  SET_ACCOUNT: 'SET_ACCOUNT',
  SET_TRANSACTION_LIST: 'SET_TRANSACTION_LIST',
  SET_ITEM_TRANSACTION: 'SET_ITEM_TRANSACTION',
  SET_SHOW_AVATAR: 'SET_SHOW_AVATAR',
});

export function getPersonInfo(dispatch, getState) {
  dispatch({ type: PersonActionTypes.GET_PERSON_INFO });
  SystemCore.send({
    command: 'get-person-info',
  })
    .then((res) => {
      dispatch({
        type: PersonActionTypes.GET_PERSON_INFO_SUCCESS,
        payload: res,
      });
    })
    .catch((err) => {
      dispatch({ type: PersonActionTypes.GET_PERSON_INFO_FAIL, payload: err });
    });
}

export const createAccount = (value, callback) => async (dispatch) => {
  // send to mobile
  dispatch({ type: PersonActionTypes.SET_PERSON_INFO });
  SystemCore.send({
    command: 'set-person-info',
    value: value,
  })
    .then((res) => {
      dispatch({
        type: PersonActionTypes.SET_PERSON_INFO_SUCCESS,
        payload: value,
      });
      return callback?.('success');
    })
    .catch((err) => {
      dispatch({ type: PersonActionTypes.SET_PERSON_INFO_FAIL, payload: err });
      return callback?.();
    });
};

const setTransactionList = (transactionList) => async (dispatch) => {
  return dispatch(
    createAccount(PersonActionTypes.SET_TRANSACTION_LIST, transactionList),
  );
};

export const getTransactionList = () => async (dispatch) => {
  const ListTransaction = [];
  dispatch(setTransactionList(ListTransaction));
};

export const setItemTransaction =
  (transaction, callback) => async (dispatch) => {
    dispatch(createAction(PersonActionTypes.SET_ITEM_TRANSACTION, transaction));
    callback?.();
    return;
  };

export const setShowAvatar = (isShow) => async (dispatch) => {
  return dispatch(createAction(PersonActionTypes.SET_SHOW_AVATAR, isShow));
};
