import React, { useEffect } from "react";
import Lottie from "lottie-web";
import LoadingJson from "./loading-animate.json";
import classNames from "classnames";

export interface LoadingLottieProps {
  height?: number;
  size?: "xs" | "sm" | "md" | "lg";
  className?: string;
}

const LoadingLottie: React.FC<LoadingLottieProps> = ({ height = 150, size, className }) => {
  useEffect(() => {
    if (document.getElementById("lottie")) {
      Lottie.loadAnimation({
        container: document.getElementById("lottie") as HTMLElement, // Required
        animationData: LoadingJson, // Required
        renderer: "svg", // Required
        loop: true, // Optional
        autoplay: true, // Optional
        name: "loading", // Name for future reference. Optional.
      });
    }
  }, []);

  return (
    <div className={classNames("flex justify-center items-center", className)} style={{ height: height }}>
      <div style={{ height: height }} id="lottie"></div>
    </div>
  );
};

LoadingLottie.defaultProps = {
  size: "lg",
};

export default LoadingLottie;
