import React from 'react';
import styled, { css } from 'styled-components';

const FlexBox = ({
  children,
  alignItem,
  justifyContent,
  flexDirection,
  flexWrap,
  gap,
  flex,
  mt,
  mr,
  mb,
  ml,
}) => {
  return (
    <Root
      className="FlexBox"
      alignItem={alignItem}
      justifyContent={justifyContent}
      flexDirection={flexDirection}
      flexWrap={flexWrap}
      gap={gap}
      flex={flex}
      mt={mt}
      mr={mr}
      mb={mb}
      ml={ml}>
      {children}
    </Root>
  );
};

export const FlexItem = ({
  children,
  alignSefl,
  justifySelf,
  width,
  height,
  flex,
}) => {
  return (
    <RootItem
      className="FlexIem"
      alignSefl={alignSefl}
      justifySelf={justifySelf}
      height={height}
      width={width}
      flex={flex}>
      {children}
    </RootItem>
  );
};

export default FlexBox;

const Root = styled.div`
  ${({
    alignItem,
    justifyContent,
    flexDirection,
    flexWrap,
    gap,
    flex,
    mt,
    mr,
    mb,
    ml,
  }) => {
    return css`
      display: flex;
      flex-direction: ${flexDirection};
      justify-content: ${justifyContent};
      align-items: ${alignItem};
      flex-wrap: ${flexWrap};
      gap: ${gap};
      flex: ${flex};
      margin-top: ${mt};
      margin-right: ${mr};
      margin-bottom: ${mb};
      margin-left: ${ml};
    `;
  }}
`;

const RootItem = styled.div`
  ${({ alignSefl, justifySelf, width, height, flex }) => {
    return css`
      flex: ${flex};
      justify-self: ${justifySelf};
      align-self: ${alignSefl};
      width: ${width};
      height: ${height};
    `;
  }}
`;
