import React, { FC, PropsWithChildren } from "react";
import { LoadingLottie } from "../../atoms";
import { WithClassName } from "../../../types";

interface LoadingTransparentProps extends PropsWithChildren<WithClassName> {
  blocking: boolean;
  full?: boolean;
  style?: React.CSSProperties;
  height?: number;
}

const LoadingTransparent: FC<LoadingTransparentProps> = (props) => {
  return (
    <>
      {props.blocking && (
        <div className="backdrop-overlay" style={{ height: props.height ?? 250 }}>
          {props.blocking && <LoadingLottie height={props.height ?? 250} />}
        </div>
      )}
      {props.children}
    </>
  );
};

export default LoadingTransparent;
