import React from 'react';
import { CheckBoxWrapper } from './styled';
import PropTypes from 'prop-types';

function CheckBox({
  onChange,
  value = false,
  disable = false,
  type = 'square',
  isOutLine,
  bg = 'primary'
}) {
  const HandleClick = () => {
    !disable && onChange?.(!value);
    // SystemCore.send({
    //   command: "vibrate",
    //   value: {
    //     type: "soft",
    //   },
    // });
  };

  return (
    <CheckBoxWrapper
      check={value}
      disable={disable}
      isOutLine={isOutLine}
      onClick={HandleClick}
      className={`${value && 'checked'} ${type} ${bg}`}>
      <div className="tick_mark"></div>
    </CheckBoxWrapper>
  );
}

CheckBox.propTypes = {
  type: PropTypes.oneOf(['circle', 'square']),
};
export default CheckBox;
