import ModalSearchEngine from "../SearchEngineModal";

function App({ visible, onClose }) {
  const [showModalSearchEngine, setShowModalSearchEngine] = useState(false);

  const dataFunc = useMemo(
    () => [
      {
        id: 1,
        title: "Default search engine",
        icon: iconSearch,
        onClick: () => setShowModalSearchEngine(true),
      },
      {
        id: 2,
        title: "Add shortcut to D-App Page",
        icon: iconAddShortCut,
        onClick: () => {},
      },
      {
        id: 4,
        title: "Add to Bookmark",
        icon: iconBook,
        onClick: handleAddBookMark,
      },
      {
        id: 5,
        title: "Exit",
        color: "#FF0366",
        icon: iconExit,
        onClick: () => {},
      },
    ],
    []
  );

  return (
    <ModalBase visible={visible} onClose={onClose} style={{}}>
      <Thumb />
      <List>
        {dataCate.map((item) => (
          <Item style={{ background: item.background }} key={item.id} onClick={item.onClick}>
            <img src={item.icon} alt={"icon"} />
            <span className="title">{item.title}</span>
          </Item>
        ))}
      </List>
      <ListFuc>
        {dataFunc.map((item) => (
          <div className="item" onClick={item.onClick}>
            <span>{item.title}</span>
            <img className="icon" src={item.icon} alt={"icon"} />
          </div>
        ))}
      </ListFuc>
      <ModalSearchEngine visible={showModalSearchEngine} onClose={() => setShowModalSearchEngine(false)} />
    </ModalBase>
  );
}

export default App;

