import { SystemCore } from '@@packages/core';
import createAction from '../action';

export const sendingCoinActionTypes = Object.freeze({
  SET_TRANSACTION_DETAIL: 'SET_TRANSACTION_DETAIL',
  SET_HISTORY_SENDING_COIN: 'SET_HISTORY_SENDING_COIN',
  SET_WALLET_RECEIVE_COIN: 'SET_WALLET_RECEIVE_COIN',
  DELETE_WALLET_RECEIVE_COIN: 'DELETE_WALLET_RECEIVE_COIN',
  SET_SENDING_COIN_DETAIL: 'SET_SENDING_COIN_DETAIL',
  DELETE_SENDING_COIN_DETAIL: 'DELETE_SENDING_COIN_DETAIL',
  SET_OTP_SENDING: 'SET_OTP_SENDING',
  DELETE_OTP_SENDING: 'DELETE_OTP_SENDING',

  SEND_TRANSACTION: 'SEND_TRANSACTION',
  SEND_TRANSACTION_SUCCESS: 'SEND_TRANSACTION_SUCCESS',
  SEND_TRANSACTION_FAIL: 'SEND_TRANSACTION_FAIL',

  GET_WHITE_LIST: 'GET_WHITE_LIST',
  GET_WHITE_LIST_SUCCESS: 'GET_WHITE_LIST_SUCCESS',
  GET_WHITE_LIST_FAIL: 'GET_WHITE_LIST_FAIL',
});

