export const getHistorySendingCoin =
  (data = {}) =>
  (dispatch) => {
    dispatch({ type: sendingCoinActionTypes.GET_WHITE_LIST });
    SystemCore.send({
      command: 'get-white-list-pagination',
      value: {
        page: data.page || 1,
        limit: data.limit || 10,
      },
    })
      .then((res) => {
        dispatch({
          type: sendingCoinActionTypes.GET_WHITE_LIST_SUCCESS,
          payload: res,
        });
      })
      .catch((err) => {
        dispatch({
          type: sendingCoinActionTypes.GET_WHITE_LIST_FAIL,
          payload: err,
        });
      });
  };

