export const setWalletReceiveCoin = (wallet, callback) => async (dispatch) => {
  dispatch(
    createAction(sendingCoinActionTypes.SET_WALLET_RECEIVE_COIN, wallet),
  );

  return callback?.();
};

export const deleteWalletReceiveCoin = () => async (dispatch) => {
  return dispatch(
    createAction(sendingCoinActionTypes.DELETE_WALLET_RECEIVE_COIN),
  );
};

export const setSendingCoinDetail = (detail, callback) => async (dispatch) => {
  dispatch(
    createAction(sendingCoinActionTypes.SET_SENDING_COIN_DETAIL, detail),
  );
  callback?.();
  return;
};
export const setTransactionDetail = (data) => (dispatch) => {
  dispatch(createAction(sendingCoinActionTypes.SET_TRANSACTION_DETAIL, data));
};

export const deleteSendCoinDetail = (callback) => async (dispatch) => {
  dispatch(createAction(sendingCoinActionTypes.DELETE_SENDING_COIN_DETAIL));
  callback?.();
  return;
};

export const pushSendCoinToMobile = (detail) => (dispatch) => {
  dispatch({ type: sendingCoinActionTypes.SEND_TRANSACTION });
  SystemCore.send({
    command: 'send-transaction',
    value: detail,
  })
    .then((res) => {
      dispatch({
        type: sendingCoinActionTypes.SEND_TRANSACTION_SUCCESS,
        payload: res,
      });
    })
    .catch((err) => {
      dispatch({
        type: sendingCoinActionTypes.SEND_TRANSACTION_FAIL,
        payload: err,
      });
    });
  // const OPT = (Math.random() * 100000).toString().substring(0, 4);
  // dispatch(createAction(sendingCoinActionTypes.SET_OTP_SENDING, OPT));
  // return;
};
