import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faCheck,
  faExclamation,
  faPaperPlane,
  faFileZipper,
  faTrash,
} from '@fortawesome/free-solid-svg-icons';
import Proptypes from 'prop-types';
import { Img, Root } from './styled';

const color = (type) => {
  switch (type) {
    case 'success':
      return 'linear-gradient(45deg, #04b800, #04b800, #ffdf00)';
    case 'warning':
      return 'linear-gradient(45deg, #FC9D00, #FFDF00, #FF0366)';
    case 'delete':
    case 'error':
      return 'linear-gradient(45deg, #FF0366,#FF0366,#FF0366, #FFDF00, #FC9D00)';
    case 'sending':
    case 'file':
      return 'linear-gradient(45deg, #004DFF, #0084F4, #FF0366,#FC9D00)';
    default:
      return 'linear-gradient(45deg, #04b800, #04b800, #ffdf00)';
  }
};

const IconStatus = ({ type = 'success', icon, imageIcon }) => {
  const render = () => {
    switch (type) {
      case 'error':
        return {
          icon: faExclamation,
          color: color(type),
        };
      case 'warning':
        return {
          icon: faExclamation,
          color: color(type),
        };
      case 'sending':
        return {
          icon: faPaperPlane,
          color: color(type),
        };
      case 'file':
        return {
          icon: faFileZipper,
          color: color(type),
        };
      case 'delete':
        return {
          icon: faTrash,
          color: color(type),
        };
      default:
        return {
          icon: faCheck,
          color: color(type),
        };
    }
  };
  return (
    <Root color={render().color}>
      <Img src={ImgEf} />
      <div className="gradient"></div>
      {imageIcon ? (
        <img className="imageIcon" src={imageIcon} alt={'Icon'} />
      ) : (
        <FontAwesomeIcon icon={icon || render().icon} />
      )}
    </Root>
  );
};

IconStatus.propTypes = {
  type: Proptypes.oneOf([
    'success',
    'error',
    'sending',
    'file',
    'delete',
    'warning',
  ]),
  icon: Proptypes.oneOfType([String]),
  imageIcon: Proptypes.oneOfType([String]),
};

export default IconStatus;
export const ImgEf =
  'data:image/png+xml;base64,';
