import { sendingCoinActionTypes } from './action';

const initState = {
  list_sending_coin_history: [],
  wallet_Receive_Coin: {},
  list_token_in_person: [],
  sending_coin_detail: {},
  opt_sending: '',

  statusSendTransaction: 0,

  statusGetWhiteList: 0,
  errorGetWhiteList: '',
  whiteListData: [],

  transactionDetail: {},
};

export default function sendingCoinReducer(state = initState, action) {
  switch (action.type) {
    case sendingCoinActionTypes.SET_TRANSACTION_DETAIL:
      return { ...state, transactionDetail: { ...action.payload } };

    case sendingCoinActionTypes.SET_HISTORY_SENDING_COIN:
      return { ...state, list_sending_coin_history: [...action.payload] };
    case sendingCoinActionTypes.SET_WALLET_RECEIVE_COIN:
      return { ...state, wallet_Receive_Coin: action.payload };

    case sendingCoinActionTypes.SEND_TRANSACTION:
      return { ...state, statusSendTransaction: 0 };
    case sendingCoinActionTypes.SEND_TRANSACTION_SUCCESS:
      return { ...state, statusSendTransaction: 1 };
    case sendingCoinActionTypes.SEND_TRANSACTION_FAIL:
      return { ...state, statusSendTransaction: 2 };

    case sendingCoinActionTypes.GET_WHITE_LIST:
      return { ...state, statusGetWhiteList: 0, errorGetWhiteList: '' };
    case sendingCoinActionTypes.GET_WHITE_LIST_SUCCESS:
      return {
        ...state,
        statusGetWhiteList: 1,
        whiteListData: action.payload.data.data,
      };
    case sendingCoinActionTypes.GET_WHITE_LIST_FAIL:
      return {
        ...state,
        statusGetWhiteList: 2,
        errorGetWhiteList: action.payload,
      };

    case sendingCoinActionTypes.DELETE_WALLET_RECEIVE_COIN:
      return { ...state, wallet_Receive_Coin: {} };
    case sendingCoinActionTypes.SET_LIST_TOKEN_ON_PERSON:
      return { ...state, list_token_in_person: [...action.payload] };
    case sendingCoinActionTypes.SET_SENDING_COIN_DETAIL:
      return { ...state, sending_coin_detail: { ...action.payload } };
    case sendingCoinActionTypes.DELETE_SENDING_COIN_DETAIL:
      return { ...state, sending_coin_detail: {} };
    case sendingCoinActionTypes.SET_OTP_SENDING:
      return { ...state, opt_sending: action.payload };
    case sendingCoinActionTypes.DELETE_OTP_SENDING:
      return { ...state, opt_sending: '' };
    default:
      return { ...state };
  }
}
