import classNames from "classnames";
import React from "react";
import { WithChildren } from "../../../types";

interface BoxProps extends WithChildren {
  onClick?: () => void;
  style?: React.CSSProperties;
  className?: string;
}

export const Box = React.forwardRef<HTMLDivElement, BoxProps>(({ children, onClick, style, className }, ref) => {
  return (
    <div ref={ref} className={classNames("box", className)} onClick={onClick} style={style}>
      {children}
    </div>
  );
});

export default Box;
