import React from 'react';
import ReactDOM from 'react-dom/client';
import { v4 as uuidv4 } from 'uuid';

interface UploadImageOptions {
  multiple?: boolean;
}

function useUploadImage(options: UploadImageOptions = {}) {
  const id = React.useMemo(() => uuidv4(), []);
  const inputFileRef = React.useRef<HTMLInputElement>(null);
  const [file, setFile] = React.useState<HTMLInputElement['value']>();
  const [fileList, setFileList] = React.useState<FileList>();
  const [images, setImages] = React.useState<string[]>();
  const [uploadRoot, setUploadRoot] = React.useState<ReactDOM.Root>();

  const onUpload = React.useCallback(() => {
    if (!inputFileRef || !inputFileRef.current) return;
    const input = inputFileRef.current;

    input.click();
  }, [inputFileRef]);

  const onChange = React.useCallback((options: UploadImageOptions) => {
    return (e: React.ChangeEvent<HTMLInputElement>) => {
      try {
        if (!e.target.files) return;
        const files = e.target.files;
        setFileList(files);
        setFile(e.target.value);

        if (options.multiple) {
          setImages(() => {
            const arr: string[] = [];
            for (const key in files) {
              if (Object.prototype.hasOwnProperty.call(files, key)) {
                const element = files[key];
                arr.push(URL.createObjectURL(element));
              }
            }
            return arr;
          });
          return;
        }
        setImages([URL.createObjectURL(files[0])]);
      } catch (error) {
        console.error(error);
      }
    };
  }, []);

  React.useEffect(() => {
    let upload = document.getElementById('upload-' + id);
    if (!upload) {
      upload = document.createElement('div');
      upload.id = 'upload-' + id;
      upload.style.display = 'none';
      document.getElementById('root')?.append(upload);
      setUploadRoot(ReactDOM.createRoot(upload));
    }

    return () => {
      uploadRoot && uploadRoot.unmount();
      upload && document.getElementById('root')?.removeChild(upload);
    };
  }, []);

  React.useEffect(() => {
    if (uploadRoot) {
      uploadRoot.render(
        <InputUploadImage
          multiple={options.multiple}
          onChange={onChange(options)}
          ref={inputFileRef}
        />,
      );
    }
  }, [uploadRoot, onChange, inputFileRef, options.multiple]);

  return {
    onUpload,
    fileList,
  };
}

const InputUploadImage: React.FC<any> = React.forwardRef<HTMLInputElement, any>(
  (props, ref) => {
    return (
      <input
        style={{ display: 'none' }}
        onChange={props.onChange}
        type={'file'}
        ref={ref}
        accept={'image/*'}
        multiple={props.multiple}
      />
    );
  },
);

export default useUploadImage;
