import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import { Root, WrapIcon } from './styled';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCircleNotch } from '@fortawesome/free-solid-svg-icons';

const Button = ({
  variant = 'container',
  title,
  disable,
  disableBackgroud,
  loading,
  onClick,
  noGradient,
  width,
  height,
  boderWidth,
  borderColor,
  type,
  style
}) => {

  const className = useMemo(() => {
    switch (variant) {
      case "outlined":
        return "Button-Outlined";
      case "container":
        return "Button-Container";
      default:
        return "Button-Container";
    }
  },[variant]);
  return (
    <Root
      style={style}
      noGradient={noGradient}
      width={width}
      height={height}
      boderWidth={boderWidth}
      borderColor={borderColor}
      disable={disable}
      disableBackgroud={disableBackgroud}
      type={type}
      loading={loading?.toString()}
      onClick={() => {
        !disable && !loading && onClick?.();
      }}
      className={`${variant} ${className}`}>
      <span className="text">{title}</span>
      {loading && (
        <WrapIcon>
          <FontAwesomeIcon icon={faCircleNotch} />
        </WrapIcon>
      )}
    </Root>
  );
};

Button.propTypes = {
  noGradient: PropTypes.bool,
  width: PropTypes.number,
  height: PropTypes.number,
  disable: PropTypes.bool,
  loading: PropTypes.bool,
  onClick: PropTypes.func,
  title: PropTypes.string,
  type: PropTypes.oneOf(['success', 'warning', 'error', 'import']),
  variant: PropTypes.oneOf(['container', 'outlined', ""]),
  disableBackgroud: PropTypes.bool,
  boderWidth: PropTypes.number,
  borderColor: PropTypes.number,
};

export default Button;
