import React, { useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { useDispatch, useSelector, shallowEqual } from 'react-redux';
import styled from 'styled-components';
import { Button, IconStatus, Flexbox, ModalBase } from '@@packages/components';
import { setNotificationBottom, resetStatusCreateWallet } from '@@packages/redux';
import { SystemCore } from '@@packages/core';

function ModalGeneral() {
  const dispatch = useDispatch();
  const history = useHistory();

  useEffect(() => {
    SystemCore.on('on-receive-message', (res) => {
      const action = res.data.action;
      const data = res.data.data || {};
      switch (action) {
        case 'open-send-screen': {
          history.push('/sending-coin');
          break;
        }
        case 'open-send-detail-screen': {
          history.push({
            pathname: '/sending-coin',
            state: data,
          });
          break;
        }
        default: {
          return null;
        }
      }
    });
  }, []);

  const { isVisible, contentNoti } = useSelector(
    (state) => state.NotificationControllerReducer,
    shallowEqual,
  );

  const handleResetCreateWallet = () => {
    dispatch(resetStatusCreateWallet());
  };

  const handleClose = () => {
    dispatch(
      setNotificationBottom({
        isVisible: false,
        contentNoti: {},
      }),
    );
    if (contentNoti.actionClose) {
      handleAction(contentNoti.actionClose);
    }
  };

  const handleAction = (action) => {
    switch (action) {
      case 'reset-status-create-wallet': {
        handleResetCreateWallet();
        break;
      }
      case 'create-wallet-go-home': {
        handleResetCreateWallet();
        history.push({
          pathname: '/home',
        });
        break;
      }
      case 'create-wallet-go-account': {
        handleResetCreateWallet();
        history.push({
          pathname: '/create-account',
        });
        break;
      }
      default: {
        return null;
      }
    }
  };

  return (
    <ModalBase
      style={{
        background: 'red',
      }}
      visible={isVisible}
      onClose={() => {
        handleClose();
      }}>
      <WrapContentError>
        <WrapImage>
          <IconStatus type={contentNoti.type || 'warning'} />
        </WrapImage>
        {contentNoti.title && <Title>{contentNoti.title}</Title>}
        {contentNoti.desc && <Des>{contentNoti.desc}</Des>}
      </WrapContentError>
      <WrapButton>
        <Flexbox gap={'var(--space-3)'}>
          {contentNoti.isHasCancel && (
            <Button
              variant="outlined"
              title={contentNoti.cancleLabel || 'No'}
              borderColor={'var(--color-neutral-5)'}
              onClick={() => {
                handleClose();
              }}
              type={contentNoti.type || 'error'}
              style={{
                marginRight: 16,
              }}
            />
          )}
          {isVisible && (
            <Button
              variant="container"
              title={contentNoti.submitLabel || 'OK'}
              type={contentNoti.type || 'success'}
              onClick={() => {
                handleClose();
              }}
              isShowConsole
            />
          )}
        </Flexbox>
      </WrapButton>{' '}
    </ModalBase>
  );
}

export default ModalGeneral;

const Title = styled.p`
  width: 100%;
  text-align: center;
  font-weight: 700;
  font-size: 22px;
  line-height: 30px;
  color: var(--color-title);
`;

const Des = styled.p`
  width: 100%;
  text-align: center;
  font-weight: 400;
  font-size: 15px;
  line-height: 21px;
  color: var(--color-des);
  margin: var(--space-1) 0;
`;

const WrapContentError = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  padding-top: var(--space-1);
`;

const WrapImage = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  margin: var(--space-1) 0;
`;

const WrapButton = styled.div`
  width: 100%;
  margin-top: var(--space-3);
`;
