import React, { useCallback, useEffect, useLayoutEffect, useMemo, useState } from "react";
import styled from "styled-components";
import Spinner from "../../molecules/Spinner";
import classNames from "classnames";
import { Animated, AnimatedProps } from "react-animated-css";
import { SystemCore } from "@@packages/core";
import { useDidUpdateEffect } from "@@packages/hooks";
import LoadingTransparent from "../LoadingTransparent";

function debounce(func, delay) {
  let timeout;

  return function executedFunc(...args) {
    if (timeout) {
      clearTimeout(timeout);
    }

    timeout = setTimeout(() => {
      func(...args);
      timeout = null;
    }, delay);
  };
}

const IconSearch = () => {
  return (
    <svg width={12} height={12} viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
      <path
        fillRule="evenodd"
        clipRule="evenodd"
        d="M12.5 11H11.71L11.43 10.73C12.41 9.59 13 8.11 13 6.5C13 2.91 10.09 0 6.5 0C2.91 0 0 2.91 0 6.5C0 10.09 2.91 13 6.5 13C8.11 13 9.59 12.41 10.73 11.43L11 11.71V12.5L16 17.49L17.49 16L12.5 11ZM6.5 11C4.01 11 2 8.99 2 6.5C2 4.01 4.01 2 6.5 2C8.99 2 11 4.01 11 6.5C11 8.99 8.99 11 6.5 11Z"
        fill={"grey"}
      />
    </svg>
  );
};

function removeAccents(str) {
  return str
    .normalize("NFD")
    .replace(/[\u0300-\u036f]/g, "")
    .replace(/đ/g, "d")
    .replace(/Đ/g, "D");
}

export interface SpinnerListWithSearchProps {
  visible: boolean;
  type: "text" | "date";
  itemsCenter?: Array<{ value: any; label: string }>;
  valueCenter?: any;
  disabled?: boolean;
  isSearch?: boolean;
  isCoreReady?: boolean;
}

const animatedProps: AnimatedProps = {
  animationIn: "zoomInUp",
  animationOut: "zoomOutDown",
  animationInDuration: 400,
  animationOutDuration: 400,
  isVisible: true,
  className: "w-full flex flex-1",
};

const SpinnerListWithSearch: React.FC<SpinnerListWithSearchProps> = ({ visible = true, isCoreReady }) => {
  const [spinnerInfo, setSpinnerInfo] = useState<Omit<SpinnerListWithSearchProps, "visible" | "isCoreReady"> & { name?: string }>(
    {
      type: "text",
      disabled: false,
      itemsCenter: [],
      valueCenter: null,
      isSearch: true,
      name: "",
    }
  );

  const { type = "text", itemsCenter = [] } = spinnerInfo;

  const [disableWhenScrolling, setDisableWhenScrolling] = useState(false);
  const [listData, setListData] = useState<Array<{ label: string; value: any }>>([]);
  const [valueData, setValueData] = useState<{ value: any; label: string }>();
  const [searchValue, setSearchValue] = useState("");
  const [loading, setLoading] = useState(true);

  const handleClose = () => {
    SystemCore.send({
      command: "spinner-close",
    });
  };

  const handleDone = (data, isDone) => {
    console.log({ data, isDone }, spinnerInfo?.name, "---------handle change-------");

    SystemCore.send({
      command: "spinner-selected",
      value: {
        isDone,
        value: data,
        name: spinnerInfo?.name,
      },
    });
  };

  const handleSearchFocus = useCallback(() => {
    if (isCoreReady) {
      SystemCore.send({ command: "searching-on-spinner", value: true });
    }
  }, [isCoreReady]);

  const handleSearchBlur = useCallback(() => {
    if (isCoreReady) {
      SystemCore.send({ command: "searching-on-spinner", value: false });
    }
  }, [isCoreReady]);

  const handleSeaching = useCallback(
    (label: string) => {
      setSpinnerInfo({
        ...spinnerInfo,
        itemsCenter: listData?.filter((data) => {
          const value = removeAccents(data.label).trim().toLowerCase();
          const i = removeAccents(label).trim().toLowerCase();
          return value.indexOf(i) > -1;
        }),
      });
    },
    [spinnerInfo, listData]
  );

  useLayoutEffect(() => {
    if (!valueData) setValueData(itemsCenter?.[0]);
  }, [itemsCenter.length]);

  useDidUpdateEffect(() => {
    if (searchValue) debounce(handleSeaching(searchValue), 400);
  }, [searchValue]);

  useEffect(() => {
    // setSpinnerInfo({
    //   ...spinnerInfo,
    //   itemsCenter: [
    //     { label: 'test', value: 'test' },
    //     { label: 'test1', value: 'test1' },
    //     { label: 'tes2t', value: 'tes2t' },
    //   ],
    // });
    // setListData([
    //   { label: 'test', value: 'test' },
    //   { label: 'test1', value: 'test1' },
    //   { label: 'tes2t', value: 'tes2t' },
    // ]);
    if (isCoreReady) {
      SystemCore.send({ command: "get-data", value: { type: "spinner" } })
        .then((res) => {
          // console.log('-------------stating-------------- get data info', res);
          const getValue = res.data?.value ?? undefined;
          setSpinnerInfo({
            ...spinnerInfo,
            ...res.data,
            valueCenter: getValue,
          });
          setListData(res.data.itemsCenter ?? spinnerInfo.itemsCenter);
          setLoading(false);
          setValueData(res.data.valueCenter);
        })
        .catch((err) => {
          setLoading(false);
          // console.log(err, '------------------');
        });
      // SystemCore.on("on-listen-info", (res: any) => {
      //   console.log({ res }, "------------n-listen-info-----------");
      // });
    }
    //  else {
    //   setLoading(false);
    // }
  }, [isCoreReady]);

  const handleClear = useCallback(() => {
    setSearchValue("");
  }, []);

  const heightContent = useMemo(() => 308, []);

  const isSearch = useMemo(() => spinnerInfo.isSearch || false, [spinnerInfo.isSearch]);

  console.log({ valueData }, "--------value change------");

  return (
    // itemsCenter?.length === 0
    <LoadingTransparent blocking={loading} height={heightContent}>
      <WrapContent isSearch={isSearch} visible={visible} height={heightContent}>
        <Animated {...animatedProps}>
          {spinnerInfo.isSearch ? (
            <WrapInput className={classNames("display-center toggle-search")}>
              <div className="search">
                <div className="left-2 absolute-left z-1">
                  <IconSearch />
                </div>
                <input
                  value={searchValue}
                  onChange={(e) => setSearchValue(e.target.value)}
                  className=" left-6 search__input absolute-left"
                  onFocus={handleSearchFocus}
                  onBlur={handleSearchBlur}
                  name="search"
                  id="searching"
                  placeholder="Tìm kiếm"
                />
                {!!searchValue && (
                  <button onClick={handleClear} className={"absolute-right"}>
                    <svg fill="none" width={32} height={32} viewBox="0 0 32 32" xmlns="http://www.w3.org/2000/svg">
                      <path
                        fill={"grey"}
                        d="M23.5321 8.46789C22.9081 7.84404 21.8965 7.84404 21.2724 8.46789L16.01 13.7303L10.7476 8.46789C10.1205 7.86227 9.12381 7.87093 8.50738 8.48736C7.89095 9.10379 7.88229 10.1005 8.48791 10.7276L13.7503 15.99L8.48791 21.2524C8.07248 21.6537 7.90587 22.2478 8.05212 22.8066C8.19837 23.3653 8.63471 23.8016 9.19345 23.9479C9.75218 24.0941 10.3463 23.9275 10.7476 23.5121L16.01 18.2497L21.2724 23.5121C21.8995 24.1177 22.8962 24.1091 23.5126 23.4926C24.1291 22.8762 24.1377 21.8795 23.5321 21.2524L18.2697 15.99L23.5321 10.7276C24.156 10.1035 24.156 9.09194 23.5321 8.46789Z"
                      />
                    </svg>
                  </button>
                )}
              </div>
            </WrapInput>
          ) : null}
        </Animated>
        <div className="button-container">
          <button onClick={handleClose} disabled={disableWhenScrolling}>
            Close
          </button>
          {/* {spinnerInfo.isSearch && (
            <button onClick={handle} className={"absolute-center display-center gap-2 done"}>
              {! ? "Search" : "Cancel"}
            </button>
          )} */}
          <button
            className="done"
            disabled={disableWhenScrolling}
            onClick={() => {
              handleDone(valueData, true);
              handleClose();
            }}
          >
            Done
          </button>
        </div>

        <div className="content-container">
          {!!itemsCenter?.length ? (
            <Spinner
              isScrolling={(val: boolean) => setDisableWhenScrolling(val)}
              items={itemsCenter}
              value={valueData}
              type={"center"}
              onChange={(v) => {
                debounce(setValueData(v), 400);
                debounce(handleDone(v, false), 400);
              }}
            />
          ) : null}
        </div>
      </WrapContent>
    </LoadingTransparent>
  );
};

export default React.memo(SpinnerListWithSearch);

