import { SettingActionTypes } from './action';

const initState = {
  row_bottomSheet: 0,
  statusGetSettingApp: undefined,
  errorGetSettingApp: "",
  mySettingApp: {},

  statusSetNofifyActive: undefined,
  errorSetNofifyActive: undefined,
};

export default function settingReducer(state = initState, action) {
  switch (action.type) {
    case SettingActionTypes.GET_SETTING_APP:
      return { ...state, statusGetSettingApp: 0, errorGetSettingApp: "" };
    case SettingActionTypes.GET_SETTING_APP_SUCCESS:
      return { ...state, statusGetSettingApp: 1, mySettingApp: {...action.payload.data}};
    case SettingActionTypes.GET_SETTING_APP_FAIL:
      return { ...state, statusGetSettingApp: 2, mySettingApp: {}, errorGetSettingApp: action.payload.message };
    case SettingActionTypes.SETTING_STATUS_NOTIFY:
      return { ...state, statusSetNofifyActive: 0, errorSetNofifyActive: "" };
    case SettingActionTypes.SETTING_STATUS_NOTIFY_SUCCESS:
      return { ...state, statusSetNofifyActive: 1, mySettingApp: {...state.mySettingApp, ...action.payload.data}};
    case SettingActionTypes.SETTING_STATUS_NOTIFY_FAIL:
      return { ...state, statusSetNofifyActive: 2, errorSetNofifyActive: action.payload.message, mySettingApp: {...state.mySettingApp} };
    case SettingActionTypes.SET_ROW_BOTTOM_SHEET:
      return { ...state, row_bottomSheet: action.payload };
    default:
      return { ...state };
  }
}
