export class EventEmitter {
  private static _listeners: any;
  constructor() {
    EventEmitter._listeners = {};
    this.on = this.on.bind(this);
    this.removeEventListener = this.removeEventListener.bind(this);
    this.emit = this.emit.bind(this);
  }

  on(event: string | number, listener: any) {
    if (!EventEmitter._listeners[event]) {
      EventEmitter._listeners[event] = [];
    }
    EventEmitter._listeners[event].push(listener);
    return this;
  }

  removeEventListener(event: string | number, listener: any) {
    if (EventEmitter._listeners[event]) {
      EventEmitter._listeners[event] = EventEmitter._listeners[event].filter(
        (l) => l !== listener,
      );
    }
  }
  removeAllEventListeners(event) {
    if (EventEmitter._listeners[event]) {
      delete EventEmitter._listeners[event];
    }
  }

  emit(event: string | number, ...args: any[]) {
    const cbs = EventEmitter._listeners[event];
    if (cbs) {
      cbs.forEach((cb) => cb(...args));
    }
  }
}
