import SwipeableBottomSheet from "react-swipeable-bottom-sheet";
import React from "react";
import classNames from "classnames";
import { WithChildren } from "../../../types";
import Box from "../../atoms/Box";
import Icon from "../../atoms/Icon";

export interface ModalNewProps extends WithChildren {
  open: boolean;
  onChange: () => void;
  isTopIcon?: boolean;
  maxHeight?: string | number;
}

const ModalNew = React.forwardRef<HTMLDivElement, ModalNewProps>(
  ({ open, onChange, children, isTopIcon, maxHeight, ...props }, ref) => {
    return (
      <div className={classNames("m-ModalNew")} ref={ref}>
        <SwipeableBottomSheet
          swipeableViewsProps={{ disabled: true }}
          style={{ zIndex: 1000 }}
          bodyStyle={{
            background: "rgba(0,0,0,0)",
            maxHeight: maxHeight,
          }}
          open={open}
          onChange={onChange}
          {...props}
        >
          {isTopIcon && (
            <Box className={"m-ModalNew_top-icon absolute"} onClick={onChange}>
              <Icon icon="arrowDown" />
            </Box>
          )}
          <div
            className={classNames("m-ModalNew_content", {
              "m-ModalNew-isTopIcon": isTopIcon,
            })}
          >
            {children}
          </div>
        </SwipeableBottomSheet>
      </div>
    );
  }
);

export default ModalNew;
