import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus, faMinus } from '@fortawesome/free-solid-svg-icons';
import { Circle, CircleSecondary, Root, RootSecondary } from './styled';

const SwitchButton = ({
  switchType = 'primary',
  value,
  onSwitch,
  iconLeft = faPlus,
  iconRight = faMinus,
}) => {
  switch (switchType) {
    case 'secondary':
      return (
        <RootSecondary
          move={value}
          onClick={() => {
            onSwitch?.(!value);
          }}>
          <CircleSecondary move={value} />
          <FontAwesomeIcon icon={iconLeft} className="faUpDownLeftRight" />
          <FontAwesomeIcon icon={iconRight} className="faTrash" />
        </RootSecondary>
      );

    default:
      return (
        <Root
          move={value}
          onClick={() => {
            onSwitch?.(!value);
          }}>
          <Circle move={value} />
        </Root>
      );
  }
};

SwitchButton.propTypes = {
  value: PropTypes.bool,
  onSwitch: PropTypes.func,
  switchType: PropTypes.oneOf(['primary', "secondary"]),
};

export default SwitchButton;
