import { SystemCore } from '@@packages/core';
import createAction from '../action';

export const WalletActionTypes = Object.freeze({
  GET_WALLET_INFO: 'GET_WALLET_INFO',
  GET_WALLET_INFO_SUCCESS: 'GET_WALLET_INFO_SUCCESS',
  GET_WALLET_INFO_FAIL: 'GET_WALLET_INFO_FAIL',
  GET_WALLET_BALANCE: 'GET_WALLET_BALANCE',
  GET_WALLET_BALANCE_SUCCESS: 'GET_WALLET_BALANCE_SUCCESS',
  GET_WALLET_BALANCE_FAIL: 'GET_WALLET_BALANCE_FAIL',

  SET_WALLET_SELECT_IN_WALLET: 'SET_WALLET_SELECT_IN_WALLET',

  SET_WALLET_SELECT_VIEW_DETAIL: 'SET_WALLET_SELECT_VIEW_DETAIL',

  UPDATE_ENVIRON_WALLET: 'UPDATE_ENVIRON_WALLET',

  UPDATE_WALLET_NAME: 'UPDATE_WALLET_NAME',

  SET_WALLET_DEFAULT: 'SET_WALLET_DEFAULT',

  SET_MY_DEFAULT_TOKEN: 'SET_MY_DEFAULT_TOKEN',

  DELETE_WALLET: 'DELETE_WALLET',

  UPDATE_WALLET_DATA: 'UPDATE_WALLET_DATA',
});

export function getWalletInfo(dispatch, getState) {
  dispatch({ type: WalletActionTypes.GET_WALLET_INFO });
  SystemCore.send({
    command: 'get-wallet',
  })
    .then((res) => {
      dispatch({
        type: WalletActionTypes.GET_WALLET_INFO_SUCCESS,
        payload: res,
      });
    })
    .catch((err) => {
      dispatch({ type: WalletActionTypes.GET_WALLET_INFO_FAIL, payload: err });
    });
}

export const getBalanceWallet = (listWallet) => async (dispatch) => {
  dispatch({ type: WalletActionTypes.GET_WALLET_BALANCE });
  SystemCore.send({
    command: 'get-balance-wallet',
    value: {
      wallets: listWallet,
    },
  })
    .then((res) => {
      dispatch({
        type: WalletActionTypes.GET_WALLET_BALANCE_SUCCESS,
        payload: res,
      });
    })
    .catch((err) => {
      dispatch({
        type: WalletActionTypes.GET_WALLET_BALANCE_FAIL,
        payload: err,
      });
    });
};

