export const setWalletSelectInWallet = (wallet) => async (dispatch) => {
  return dispatch(
    createAction(WalletActionTypes.SET_WALLET_SELECT_IN_WALLET, wallet),
  );
};
export const updateWallet = (wallet) => async (dispatch) => {
  dispatch({
    type: WalletActionTypes.UPDATE_WALLET_DATA,
    payload: wallet,
  });
};

export const setWalletSelectViewDetail =
  (wallet, callBack) => async (dispatch) => {
    dispatch(
      createAction(WalletActionTypes.SET_WALLET_SELECT_VIEW_DETAIL, wallet),
    );
    return callBack?.();
  };

export const updateEnvironWallet = (value, callBack) => async (dispatch) => {
  dispatch(createAction(WalletActionTypes.UPDATE_ENVIRON_WALLET, value));
  return;
};

export const updateWalletName = (wallet, callBack) => async (dispatch) => {
  dispatch(createAction(WalletActionTypes.UPDATE_WALLET_NAME, wallet));
  return callBack?.();
};

export const setWalletDefault = (wallet, callBack) => async (dispatch) => {
  dispatch(createAction(WalletActionTypes.SET_WALLET_DEFAULT, wallet));
  return callBack?.();
};

export const setMyDefaultToken = (wallet, callBack) => async (dispatch) => {
  dispatch(createAction(WalletActionTypes.SET_MY_DEFAULT_TOKEN, wallet));
  return callBack?.();
};

export const deleteWallet = (address, callBack) => async (dispatch) => {
  dispatch(createAction(WalletActionTypes.DELETE_WALLET, address));
  return callBack?.();
};
