// import { act } from 'react-dom/test-utils';
import { NodeManageActionTypes } from './action';

const initState = {
  listNode: [],
  statusGetNode: -1,
  errorGetNodeMessage: '',
  statusConnectNode: -1,
  errorConnectNodeMessage: '',
};

export default function nodeManageReducer(state = initState, action) {
  switch (action.type) {
    case NodeManageActionTypes.GET_NODE_LIST:
      return {
        ...state,
        statusGetNode: 0,
        errorGetNodeMessage: '',
      };
    case NodeManageActionTypes.GET_NODE_LIST_SUCCESS:
      return {
        ...state,
        statusGetNode: 1,
        listNode: action.payload.data,
      };
    case NodeManageActionTypes.GET_NODE_LIST_FAIL:
      return {
        ...state,
        statusGetNode: 2,
        errorGetNodeMessage: action.payload,
      };

    case NodeManageActionTypes.CONNECT_NODE:
      return {
        ...state,
        statusConnectNode: 0,
        errorConnectNodeMessage: '',
      };
    case NodeManageActionTypes.CONNECT_NODE_SUCCESS:
      return {
        ...state,
        statusConnectNode: 1,
      };
    case NodeManageActionTypes.CONNECT_NODE_FAIL:
      return {
        ...state,
        statusConnectNode: 2,
        errorConnectNodeMessage: action.payload,
      };
    default:
      return { ...state };
  }
}
