import { SystemCore } from '@@packages/core';
import { faArrowRight } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useState, useCallback, SetStateAction } from 'react';
import { Input, ModalBase, Thumb } from '@@packages/components';
import styled from 'styled-components';

interface CreateFolderProps {
  open: boolean,
  onClose: () => void,
  folders: any[],
  setFolders: React.Dispatch<SetStateAction<any[]>>
}

const CreateFolder = ({ open, onClose, folders, setFolders }: CreateFolderProps) => {
  const [name, setName] = useState("");
  const [error, setError] = useState(false);

  const handleCreateFolder = (name) => {
    if(!name) return
    if(folders?.filter(f => f.title === name)?.length > 0) {
      return setError(true)
    }
    SystemCore.send({
      command: 'create-bookmark-folder',
      value: {
        name,
      }
    })
    .then((res) => {
      if (res.success) {
        setFolders?.(folders => ([...folders, { id: res.data.id, title: res.data.title, desc: res.data.desc }]))
        onClose()
      }
    })
    .catch((err) => {});
  }

  const handleClickCreate = () => {
    if(name){
      handleCreateFolder(name);
      setName("");
    }
  };

  return (
    <ModalBase visible={open} onClose={onClose} style={{}}>
      <Thumb />
      <Title>Create new folder</Title>
      <WrapInput>
        <Input value={name} onChange={(v) => setName(v)} placeholder="Type..." showIcon={false}/>
        { error && <ErrorText>Folder name already exists</ErrorText> }
        <ButtonNext onClick={handleClickCreate}>
          <FontAwesomeIcon icon={faArrowRight} color="white" fontSize="20" />
        </ButtonNext>
      </WrapInput>
    </ModalBase>
  );
};

export default CreateFolder;

const Title = styled.div`
  font-weight: 500;
  font-size: 17px;
  line-height: 23px;
  color: #004DFF;
`;
const WrapInput = styled.div`
  margin: 20px 0;
  display: flex;
  justify-content: space-between;
`;
const ButtonNext = styled.div`
  gap: 10px;
  min-width: 48px;
  height: 48px;
  background: #004DFF;
  border-radius: 10px;
  margin-left: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const ErrorText = styled.p`
  color: #FF0366;
  font-size: 12px;
  padding: 4px 0;
`