import styled, { keyframes, css } from 'styled-components';

const returnColor = (type) => {
  switch (type) {
    case 'success':
      return 'linear-gradient(90.87deg, #04B800 -41.78%, #7CCE1E 26.6%, #FDE63E 100%)';
    case 'warning':
      return 'linear-gradient(90.87deg, #FC9D00 -41.78%, #FDBE00 28.81%, #FFDF00 100%)';
    case 'error':
      return 'linear-gradient(90.87deg, #FF0366 -41.78%, #FD552F 30.29%, #FC9D00 100%)';
    case 'import':
      return 'linear-gradient(90.87deg, #554CFF -41.78%, #6C64FF 29.55%, #827CFF 100%)';
    default:
      return 'linear-gradient(90.87deg, #004DFF -41.78%, #3584FB 32.56%, #66B5F8 100%)';
  }
};

export const returnColorNoGradient = (type) => {
  switch (type) {
    case 'success':
      return 'var(--color-accent-blue-1)';
    case 'warning':
      return 'var(--color-accent-orange-1)';
    case 'delete':
    case 'error':
      return 'var(--color-accent-pink-1)';
    case 'import':
      return 'var(--color-accent-purple-1)';
    default:
      return 'var(--color-accent-blue-1)';
  }
};

const gradient = keyframes`
  0% {background-position: 0%}
  100% {background-position: 100%}
`;

export const Root = styled.button`
  ${({
    width,
    height,
    loading,
    disable,
    disableBackgroud,
    type,
    noGradient,
    boderWidth,
    borderColor,
  }) => {
    return css`
      width: ${width || '100%'};
      height: ${height || '56px'};
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: var(--font-size-title);
      font-weight: 700;
      text-transform: uppercase;
      border: none;
      border-radius: 10px;
      font-family: 'ClearSans';
      padding: 0;
      cursor: ${disable ? '' : 'pointer'};
      overflow: hidden;
      background-size: 600% 100%;
      animation: ${gradient} 1.5s linear infinite;
      animation-direction: alternate;
      position: relative;

      &.outlined {
        -webkit-tap-highlight-color: transparent;
        background: ${loading === 'true'
        ? '#F3F6FD'
        : disable || disableBackgroud
        ? '#fff'
        : borderColor
        ? borderColor
        : noGradient
        ? returnColorNoGradient(type)
        : returnColor(type)};
        span {
          display: flex;
          justify-content: center;
          align-items: center;
          z-index: 2;
          background: ${disable ? '#D3D7DB' : returnColor(type)};
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
        }
  
        ::before {
          content: '';
          position: absolute;
          top: ${boderWidth || '2px'};
          bottom: ${boderWidth || '2px'};
          left: ${boderWidth || '2px'};
          right: ${boderWidth || '2px'};
          background-color: #fff;
          border-radius: 8px;
          margin: auto;
          z-index: 1;
          
        }
      }
     

      &.container {
        color: ${disable ? 'rgba(211, 215, 219, 1)' : ' #fff'};
        background: ${
          loading === 'true'
            ? 'var(--color-background-text)'
            : disable
            ? '#EDEEF0'
            : noGradient
            ? returnColorNoGradient(type)
            : returnColor(type)};

        // :hover {
        //   background: ${(props) => (props.disable ? '' : '#3E78FD')};
        // }
      }
    `;
  }}
`;

const rotate = keyframes`
from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
`;

export const WrapIcon = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f3f6fd;
  color: #bbcffb;
  z-index: 9;
  cursor: auto;

  svg {
    width: 20px;
    height: 20px;
    animation: ${rotate} 1s linear infinite;
  }
`;
