import React from 'react';
import styled from 'styled-components';

export default function Radio({ checked=false, onChange }) {
  return (
    <RadioButton check={checked} onClick={onChange}>
      <div className="Radio" />
    </RadioButton>
  );
}
export const RadioButton = styled.div`
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  border: 2px solid
    ${(props) => (props.check ? 'var(--color-background-btn)' : '#BBCFFB')};
  border-radius: 25px;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  .Radio {
    width: 14px;
    height: 14px;
    color: #fff;
    background-color: ${(props) =>
      props.check ? 'var(--color-background-btn)' : '#fff'};
    border-radius: 25px;
  }
`;
