import { animated } from "@@packages/utils";
import React from "react";
const { Animated } = require("react-animated-css");
type Props = {
  children: React.ReactNode;
};

const Animation = ({ children }: Props) => {
  return (
    <Animated animationIn={animated()} animationInDuration={400}>
      {children}
    </Animated>
  );
};

export default Animation;
