import React, { Suspense } from "react";

// Colors
import { mapModifiersTailWind } from "@@packages/utils";
import { AppColors } from "../../../../assets/colors";
import SmallTriangleDown from "./SmallTriangleDown";
import classNames from "classnames";

// Icon
const ArrowDown = React.lazy(() => import("./ArrowDown"));

export interface SVGIconProps {
  className?: string;
  icon?: TSVGIcon;
  color?: TAppColors;
  width?: number;
  height?: number;
  type?: "bold" | "bulk";
}
export interface SVGProps {
  className?: string;
  color?: string;
  width?: number;
  height?: number;
  type?: "bold" | "bulk";
}

const ICON = {
  arrowDown: ArrowDown,
  smallTriangleDown: SmallTriangleDown,
};

export type TSVGIcon = keyof typeof ICON;

export const IconFactory: React.FC<SVGIconProps> = ({ className, icon = "coin", color = "grey", ...props }) => {
  const Component = ICON[icon];
  return (
    <div
      className={classNames(
        mapModifiersTailWind(`svg_icon svg_icon-${icon} svg_icon-${props.type}`, className),
        "flex",
        "items-center",
        "justify-center"
      )}
    >
      <Suspense fallback={<div>...</div>}>{Component && <Component color={AppColors[color]} {...props} />}</Suspense>
    </div>
  );
};

const Icon: React.FC<SVGIconProps> = ({ type, ...props }) => {
  return <IconFactory type="bold" {...props} />;
};

export const SVGIcon: React.FC<SVGIconProps> = ({ type, ...props }) => {
  return <IconFactory type="bulk" {...props} />;
};

export default Icon;
