import { WalletActionTypes } from './action';

const initState = {
  statusGetWallet: -1,
  walletInfo: [],
  walletSelectInWallet: {},
  walletSelectViewDetail: {},
  list_token: [{ name: 'MTD', value: 'MTD', total: 1000 }],
};

const handleUpdateEnvironWallet = (value, walletInfo) => {
  return walletInfo?.map((elm) => {
    if (elm.address === value.address) {
      return {
        ...elm,
        environment: value.environment,
      };
    } else {
      return elm;
    }
  });
};

const handleUpdateWalletName = (wallet, walletInfo) => {
  return walletInfo?.map((elm) => {
    if (elm.address === wallet.address) {
      return {
        ...elm,
        name: wallet.name,
      };
    } else {
      return elm;
    }
  });
};

const handleSetWalletDefault = (wallet, walletInfo) => {
  return walletInfo?.map((elm) => {
    if (elm.address === wallet) {
      return {
        ...elm,
        is_default: true,
      };
    } else {
      return {
        ...elm,
        is_default: false,
      };
    }
  });
};

const handleSetTokenDefaultWallet = (wallet, walletInfo) => {
  return walletInfo?.map((elm) => {
    if (elm.address === wallet.address) {
      return {
        ...elm,
        token_default: wallet.token,
      };
    } else {
      return elm;
    }
  });
};

const handleDeleteWallet = (address, walletInfo) => {
  return walletInfo.filter((elm) => elm.address !== address);
};

export default function walletReducer(state = initState, action) {
  switch (action.type) {
    case WalletActionTypes.GET_WALLET_INFO:
      return {
        ...state,
        statusGetWallet: 0,
      };
    case WalletActionTypes.GET_WALLET_INFO_SUCCESS: {
      const payloadData = action.payload.data;
      return {
        ...state,
        statusGetWallet: 1,
        walletInfo: Array.isArray(payloadData.data)
          ? [...action.payload.data.data]
          : [...state.walletInfo],
        walletSelectInWallet:
          Object.keys(state.walletSelectInWallet).length > 0
            ? state.walletSelectInWallet
            : action.payload.data.data.length > 0
            ? action.payload.data.data[0]
            : {},
      };
    }
    case WalletActionTypes.UPDATE_WALLET_DATA: {
      const currentWalletInfo = [...state.walletInfo];
      const walletUpdate = action.payload.data.data;
      const indexUpdate = currentWalletInfo.findIndex(
        (e) => e.address === walletUpdate.address,
      );
      currentWalletInfo[indexUpdate] = walletUpdate;
      return {
        ...state,
        walletInfo: [...currentWalletInfo],
      };
    }
    case WalletActionTypes.GET_WALLET_INFO_FAIL:
      return {
        ...state,
        statusGetWallet: 2,
      };
    case WalletActionTypes.GET_WALLET_BALANCE:
      return {
        ...state,
      };
    case WalletActionTypes.GET_WALLET_BALANCE_SUCCESS:
      return {
        ...state,
      };
    case WalletActionTypes.GET_WALLET_BALANCE_FAIL:
      return {
        ...state,
      };

    case WalletActionTypes.SET_WALLET_SELECT_IN_WALLET:
      return { ...state, walletSelectInWallet: { ...action.payload } };

    case WalletActionTypes.SET_WALLET_SELECT_VIEW_DETAIL:
      return { ...state, walletSelectViewDetail: { ...action.payload } };

    case WalletActionTypes.UPDATE_ENVIRON_WALLET:
      return {
        ...state,
        walletInfo: [
          ...handleUpdateEnvironWallet(action.payload, state.walletInfo),
        ],
        walletSelectViewDetail: {
          ...state.walletSelectViewDetail,
          environment: action.payload.environment,
        },
      };

    case WalletActionTypes.UPDATE_WALLET_NAME:
      return {
        ...state,
        walletInfo: [
          ...handleUpdateWalletName(action.payload, state.walletInfo),
        ],
        walletSelectViewDetail: {
          ...state.walletSelectViewDetail,
          name: action.payload.name,
        },
      };

    case WalletActionTypes.SET_WALLET_DEFAULT:
      return {
        ...state,
        walletInfo: [
          ...handleSetWalletDefault(action.payload, state.walletInfo),
        ],
        walletSelectViewDetail: {
          ...state.walletSelectViewDetail,
          is_default: true,
        },
      };

    case WalletActionTypes.SET_MY_DEFAULT_TOKEN:
      return {
        ...state,
        walletInfo: [
          ...handleSetTokenDefaultWallet(action.payload, state.walletInfo),
        ],
        walletSelectViewDetail: {
          ...state.walletSelectViewDetail,
          token_default: action.payload.token,
        },
      };

    case WalletActionTypes.DELETE_WALLET:
      return {
        ...state,
        walletInfo: [...handleDeleteWallet(action.payload, state.walletInfo)],
        walletSelectViewDetail: {},
        walletSelectInWallet:
          state.walletSelectInWallet?.address === action.payload
            ? {}
            : state.walletSelectInWallet,
      };

    default:
      return { ...state };
  }
}
