import createAction from '../action';

export const ProductActionTypes = Object.freeze({
  IS_CONNECT_DRIVER: 'IS_CONNECT_DRIVER',
  SET_LOADING: 'SET_LOADING',
  REFRESH: 'REFRESH',
});

export const setConnectDriver = (isConnect) => async (dispatch) => {
  return dispatch(
    createAction(ProductActionTypes.IS_CONNECT_DRIVER, isConnect),
  );
};

export const setLoading = (loading) => async (dispatch) => {
  return dispatch(createAction(ProductActionTypes.SET_LOADING, loading));
};

export const setRefresh = (value) => async (dispatch) => {
  return dispatch(createAction(ProductActionTypes.REFRESH, value));
};
