import React, { useState } from "react";
import { Button, Thumb, ModalBase } from "@@packages/components";
import styled from "styled-components";

export default function ModalFilterDay({ visible, onClose, setValueModal }) {
  const [listFilter, setListFilter] = useState({
    oneDay: {
      name: "1 DAY",
      status: true,
      nameCode: 1,
    },
    severDay: {
      name: "7 DAYS",
      nameCode: 7,
      status: false,
    },
    thirtyDay: {
      name: "30 DAYS",
      nameCode: 30,
      status: false,
    },
    ninetyDay: {
      name: "90 DAYS",
      status: false,
      nameCode: 90,
    },
    all: {
      name: "All",
      status: false,
      nameCode: "all",
    },
  });

  const handleChooseStatus = (name) => {
    const tmpListFilter = {
      oneDay: {
        name: "1 DAY",
        status: true,
        nameCode: 1,
      },
      severDay: {
        name: "7 DAYS",
        nameCode: 7,
        status: false,
      },
      thirtyDay: {
        name: "30 DAYS",
        nameCode: 30,
        status: false,
      },
      ninetyDay: {
        name: "90 DAYS",
        status: false,
        nameCode: 90,
      },
      all: {
        name: "All",
        status: false,
        nameCode: "all",
      },
    };
    tmpListFilter[name].status = !listFilter[name].status;
    setValueModal({ day: tmpListFilter[name].name });
    setListFilter(tmpListFilter);
    onClose();
  };
  return (
    <ModalBase visible={visible} onClose={onClose}>
      <Thumb />
      <WrapTitle>Filter</WrapTitle>
      <WrapContent>
        <Button
          variant={(listFilter.oneDay.status && "container") || ""}
          title={listFilter.oneDay.name}
          onClick={() => handleChooseStatus("oneDay")}
        />
        <Button
          variant={(listFilter.severDay.status && "container") || ""}
          title={listFilter.severDay.name}
          onClick={() => handleChooseStatus("severDay")}
        />
        <Button
          variant={(listFilter.thirtyDay.status && "container") || ""}
          title={listFilter.thirtyDay.name}
          onClick={() => handleChooseStatus("thirtyDay")}
        />
        <Button
          variant={(listFilter.ninetyDay.status && "container") || ""}
          title={listFilter.ninetyDay.name}
          onClick={() => handleChooseStatus("ninetyDay")}
        />
        <Button
          variant={listFilter.all.status && "container" | ""}
          title={listFilter.all.name}
          onClick={() => handleChooseStatus("all")}
        />
      </WrapContent>
    </ModalBase>
  );
}
export const WrapContent = styled.div`
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
  margin-left: -8px;
  margin-right: -8px;
  .Button-Container {
    font-size: 11px;
    height: 29px;
    padding: 0 12px;
    width: auto;
  }
`;

export const WrapTitle = styled.div`
  margin: 10px 16px 14px 0;
  font-family: "ClearSans";
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 13px;
  color: #04004d;
  letter-spacing: 0.7px;
`;
