import { ProductActionTypes } from './action';

const initState = {
  isConnect: false,
  isLoading: false,
  isReRefresh: false,
};

export default function reducer(state = initState, action) {
  switch (action.type) {
    case ProductActionTypes.SET_LOADING:
      return { ...state, isLoading: action.payload };
    case ProductActionTypes.IS_CONNECT_DRIVER:
      return { ...state, isConnect: action.payload };
    case ProductActionTypes.REFRESH:
      return {
        ...state,
        isReRefresh: action.payload,
      };
    default:
      return { ...state };
  }
}
