import React from "react";
import styled from "styled-components";
export default class Spinner extends React.Component {
  _spinRef = React.createRef();
  _trueSpinRef = React.createRef();
  _t = undefined;
  _isPressed = false;
  _idx = 0;

  componentDidUpdate() {
    this._updatePosition(this.props);
  }

  componentDidMount() {
    this._updatePosition(this.props);
  }

  _updatePosition(props) {
    if (props.value?.value) {
      const itemIdx = Math.max(
        props.items.findIndex((item) => item.value === props.value?.value),
        0
      );
      if (itemIdx !== this._idx) {
        this._idx = itemIdx;
        this._scrollToIndex(this._idx);
      }
    } else {
      const itemIdx = Math.max(
        props.items.findIndex((item) => item.value === props.value),
        0
      );
      if (itemIdx !== this._idx) {
        this._idx = itemIdx;
        this._scrollToIndex(this._idx);
      }
    }
  }

  _handleTouchStart = () => {
    this._isPressed = true;
    this.props.isScrolling?.(true);
    window.addEventListener("touchend", this._handleTouchEnd);
    window.addEventListener("mouseup", this._handleTouchEnd);
  };

  _handleTouchEnd = () => {
    window.removeEventListener("touchend", this._handleTouchEnd);
    window.removeEventListener("mouseup", this._handleTouchEnd);
    this._isPressed = false;
    const isScrolling = !!this._t;
    if (!isScrolling) {
      this._completeScroll();
    }
  };

  handleScroll = (e) => {
    const fake = this._spinRef.current;
    const real = this._trueSpinRef.current;
    if (fake && real) {
      const curPos = fake.scrollTop;
      real.scrollTo({ top: curPos });
      real.style.scrollBehavior = "";
    }
    if (this._t) {
      window.clearTimeout(this._t);
    }
    this._t = window.setTimeout(this._checkScrollEnd, 100);
  };

  _checkScrollEnd = () => {
    this._t = undefined;
    if (!this._isPressed) {
      this._completeScroll();
    }
  };

  _completeScroll = () => {
    const fake = this._spinRef.current;
    const real = this._trueSpinRef.current;
    let listItem;
    const SpinContainer = document.querySelector(`.${this.props?.className}`);
    if (SpinContainer) {
      listItem = SpinContainer.querySelectorAll(".item-spinner");
    }

    if (fake && real) {
      const curPos = fake.scrollTop;
      this._idx = Math.round(curPos / 36);
      real.style.scrollBehavior = "smooth";
      this._scrollToIndex(this._idx);

      if (listItem?.length) {
        listItem.forEach((element, index) => {
          if (index == this._idx) {
            element.style.fontSize = "24px";
            element.style.padding = "0";
          } else {
            element.style.fontSize = "10px";
          }
        });

        listItem.forEach((element, index) => {
          if (index == this._idx - 1 || index == this._idx + 1) {
            element.style.fontSize = "16px";
            if (this.props.type === "left") {
              element.style.paddingLeft = "12px";
            }
            if (this.props.type === "right") {
              element.style.paddingRight = "10px";
            }
          }
          if (index == this._idx - 2 || index == this._idx + 2) {
            element.style.fontSize = "10px";
            if (this.props.type === "left") {
              element.style.paddingLeft = "16px";
            }
            if (this.props.type === "right") {
              element.style.paddingRight = "16px";
            }
          }
        });
      }
      if (this.timOut) {
        window.clearTimeout(this.timOut);
      }
      this.timOut = setTimeout(() => {
        this.timOut = undefined;
        this.props.onChange?.(this.props.items[this._idx]);
        this.props.isScrolling?.(false);
      }, 200);
    }
  };

  _scrollToIndex(idx) {
    this._spinRef.current.scrollTo({ top: idx * 36 });
  }

  render() {
    const { items, type, className } = this.props;
    return (
      <SpinContainer className={className}>
        <Highlight top="72px" type={type.toString()} />
        <SpinRoot ref={this._trueSpinRef}>
          <Padding>
            {items.map(({ value, label }) => (
              <Item key={value} className="item-spinner" type={type.toString()}>
                {label ?? value}
              </Item>
            ))}
          </Padding>
        </SpinRoot>
        <Overlay />
        <SpinRoot
          ref={this._spinRef}
          onTouchStart={this._handleTouchStart}
          onMouseDown={this._handleTouchStart}
          onScroll={this.handleScroll}
        >
          <div style={{ height: (items.length + 4) * 36 }} />
        </SpinRoot>
      </SpinContainer>
    );
  }
}

const SpinContainer = styled.div`
  display: flex;
  flex: 1;
  position: relative;
`;

const SpinRoot = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  ::-webkit-scrollbar {
    display: none;
  }
`;

const Item = styled.div`
  height: 36px;
  line-height: 36px;
  font-weight: 500;
  text-align: center;
  padding-left: ${({ type }) => (type === "left" ? "16px" : "0px")};
  padding-right: ${({ type }) => (type === "right" ? "16px" : "0px")};
  font-size: 18px;
  color: #04004d;
  transition: all 0.3s;
`;

const Padding = styled.div`
  padding: 72px 0;
`;

const Highlight = styled.div`
  height: 38px;
  background-color: #edeef0;
  position: absolute;
  border-top-left-radius: ${({ type }) => (type === "left" || type === "center" ? "8px" : "")};
  border-bottom-left-radius: ${({ type }) => (type === "left" || type === "center" ? "8px" : "")};
  border-top-right-radius: ${({ type }) => (type === "right" || type === "center" ? "8px" : "")};
  border-bottom-right-radius: ${({ type }) => (type === "right" || type === "center" ? "8px" : "")};
  left: 0px;
  right: 0px;
  top: ${({ top }) => top};
`;

const Overlay = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(#ffffff, rgba(255, 255, 255, 0) 52%, rgba(255, 255, 255, 0) 48%, #ffffff);
`;
