import { useAppSelector } from '@@packages/redux';
import { useMemo } from 'react';
import isEmpty from 'lodash/isEmpty';

type ProgressValue = {
  label: string;
  from_value: number;
  to_value?: number;
  color?: string;
};

export type TemplateRankProps = {
  bronze?: ProgressValue;
  silver?: ProgressValue;
  gold?: ProgressValue;
  diamond?: ProgressValue;
};
export type TemplateRetailProps = {
  step1?: ProgressValue;
  step2?: ProgressValue;
  step3?: ProgressValue;
};
export type TemplateInnovationProps = {
  step1?: ProgressValue;
  step2?: ProgressValue;
  step3?: ProgressValue;
};

export interface ProgressRank {
  bgTheme: 'ranking';
  templateProps: TemplateRankProps;
}
export interface ProgressRetail {
  bgTheme: 'retail';
  templateProps: TemplateRetailProps;
}
export interface ProgressInnovation {
  bgTheme: 'innovation';
  templateProps: TemplateInnovationProps;
}

function handleProgress(
  defaultTemplate: TemplateRankProps,
  templateProps: TemplateRankProps,
): TemplateRankProps;
function handleProgress(
  defaultTemplate: TemplateRetailProps,
  templateProps: TemplateRetailProps,
): TemplateRetailProps;
function handleProgress(
  defaultTemplate: TemplateInnovationProps,
  templateProps: TemplateInnovationProps,
): TemplateInnovationProps;
function handleProgress(defaultTemplate: any, templateProps: any) {
  if (isEmpty(templateProps)) return defaultTemplate;
  return { ...defaultTemplate, ...templateProps };
}

function useProgress(props: ProgressRank): TemplateRankProps;
function useProgress(props: ProgressRetail): TemplateRetailProps;
function useProgress(props: ProgressInnovation): TemplateInnovationProps;

function useProgress(props: any): any {
  const { bgTheme, templateProps } = props;
  const lang = useAppSelector((state) => state.languageRedux);

  const rankingDefault: TemplateRankProps = useMemo(() => {
    return {
      bronze: { label: lang.txtBronze, from_value: 0, color: '#0084F4' },
      silver: {
        label: lang.txtSilver,
        from_value: 1000,
        to_value: 1000000,
        color: '#04B800',
      },
      gold: {
        label: lang.txtGold,
        from_value: 2000,
        to_value: 10000000,
        color: '#FFC700',
      },
      diamond: {
        label: lang.txtDiamond,
        from_value: 5000,
        to_value: 50000000,
      },
    };
  }, [lang]);
  const retailDefault: TemplateRetailProps = useMemo(() => {
    return {
      step1: { label: 'Nhận được thông tin', from_value: 0, color: '#B147F8' },
      step2: {
        label: 'Đánh giá',
        from_value: 1000,
        to_value: 1000000,
      },
      step3: {
        label: 'Kết quả hỗ trợ',
        from_value: 2000,
        to_value: 10000000,
      },
    };
  }, [lang]);
  const innovationDefault: TemplateRetailProps = useMemo(() => {
    return {
      step1: { label: 'Nhận được sáng kiến', from_value: 0, color: '#B147F8' },
      step2: {
        label: 'Đánh giá',
        from_value: 1000,
        to_value: 1000000,
      },
      step3: {
        label: 'Kết quả sáng kiến',
        from_value: 2000,
        to_value: 10000000,
      },
    };
  }, [lang]);

  const contentTemplate = useMemo(() => {
    switch (bgTheme) {
      case 'ranking':
        return handleProgress(rankingDefault, templateProps);
      case 'retail':
        return handleProgress(retailDefault, templateProps);
      case 'innovation':
        return handleProgress(innovationDefault, templateProps);
      default:
        return handleProgress(rankingDefault, templateProps);
    }
  }, [bgTheme, templateProps]);

  return contentTemplate;
}

export default useProgress;
